.portfolio {
  height: 100%;
  font-family: "Myriad Pro";
  background-image: url("/img/bg-work.png");
  background-repeat: no-repeat;
  background-position: bottom;
  background-color: #f9fafa;
  background-size: 100%;
  justify-content: center;
  z-index: -1;
}
.portfolio__container {
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: center;
}
.portfolio__text {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.portfolio__text-header {
  font-size: 48px;
  color: #28363d;
  text-transform: uppercase;
  line-height: 0.917;
  text-align: center;
}
.portfolio__text-desc {
  display: flex;
  font-size: 18px;
  color: #8198a0;
  line-height: 2;
  text-align: center;
}
.portfolio__projects {
  min-height: 660px;
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
}
.portfolio__img {
  width: 460px;
  height: 320px;
  background-color: #28363d;
  background-image: url("/img/photo-empty.png");
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  align-items: flex-end;
  margin-bottom: 10px;
}
.portfolio__img-preview {
  width: 100%;
  height: 100px;
  background-color: #fff;
  opacity: 0;
  flex-flow: column;
  justify-content: center;
}
.portfolio__img-header {
  font-size: 18px;
  color: #28363d;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 2;
  text-align: left;
  margin-left: 60px;
}
.portfolio__img-desc {
  font-size: 16px;
  color: #7b7b7b;
  text-transform: uppercase;
  line-height: 2.25;
  text-align: left;
  margin-left: 60px;
}
.portfolio__separator {
  margin-left: 60px;
}
.separator {
  display: flex;
  width: 100%;
  justify-content: center;
  margin: 10px 0;
}
/* Tablet */
@media only screen and (min-width: $tablet-min) and (max-width: $tablet-max) {
  .portfolio__projects {
    justify-content: center;
  }
}
/* Mobile */
@media only screen and (min-width: $mobile-min) and (max-width: $mobile-max) {
  .portfolio__img {
    width: 280px;
    height: 200px;
  }
  .portfolio__text-desc {
    width: 240px;
  }
  .portfolio__projects {
    justify-content: center;
  }
}
